import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate, ReanderSelect } from "../../../../components/helpers/field";
import {
  formatGram,
  getTglSystem,
} from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiLaporanKeuangan from "../../../../Validasi/LaporanKeuangan/ValidasiLaporanKeuangan";
import ExportTransaksiPembelian from "./pdf/ExportTransaksiPembelianHistory";
import Loading from "react-fullscreen-loading";
import ExportTransaksiPembelianPdfHistory from "./exel/ExportTransaksiPembelianPdfHistory";
import { GetDataGroup, getDataKondisiBarang } from "../../../../actions/datamaster_action";
class HeadFormLaporanPembelianBarangHistory extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      type: "SEMUA",
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  componentDidMount() {
    this.props.change("type", "SELISIH");
    this.props.dispatch(getDataKondisiBarang());
    this.props.dispatch(GetDataGroup());
    this.props.change("kode_group", "ALL");
    this.props.change("jenis_group", "ALL");
    this.props.change("kondisi", "ALL");
  }
  exportPdf() {
    ExportTransaksiPembelian(this.props.LaporanTransaksiPembelian);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  render() {
    let kondisi = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.datakondisi.forEach((list) => {
      let row = {
        name: list.kondisi_barang,
        value: list.kondisi_barang,
      };
      kondisi.push(row);
    });
    let datagroup = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.GetDataGroup.forEach((list) => {
      let row = {
        name: list.kode_group,
        value: list.kode_group,
      };
      datagroup.push(row);
    });
    let databarang = [];
    let berat = 0;
    let ongkos = 0;
    let harga = 0;
    let harga_nota = 0;
    let harga_gram = 0;
    let berat_nota = 0;
    this.props.LaporanTransaksiPembelian.forEach((element) => {
      element.detail.forEach((detail) => {
        databarang.push(detail);
        berat_nota = berat_nota + detail.berat_nota;
        berat = berat + detail.berat;
        ongkos = ongkos + detail.ongkos;
        harga = harga + detail.harga;
        harga_nota = harga_nota + detail.harga_nota;
        harga_gram = harga_gram + detail.harga_gram;
      });
    });
    const columns = [
      {
        dataField: "no_faktur_beli",
        text: "No FB",
        footerAttrs: {
          colSpan: "5",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Transaksi : {databarang.length} </div>;
        },
      },
      {
        dataField: "kode_sales",
        text: "Kode Sales",
      },
      {
        dataField: "kode_member",
        text: "Kode Member",
      },
      {
        dataField: "nama_customer",
        text: "Nama Member",
      },
      {
        dataField: "nama_barang",
        text: "Nama Barang",
      },
      {
        dataField: "kondisi",
        text: "Kondisi",
        footer: "",
      },

      {
        dataField: "berat_nota",
        text: "Berat Nota",
        headerClasses: "text-right",
        formatter: (cell) => {
          return <div className="text-right"> {formatGram(cell, 3)} </div>;
        },
      },
      {
        dataField: "berat",
        text: "Berat",
        headerClasses: "text-right",
        formatter: (cell) => {
          return <div className="text-right"> {formatGram(cell, 3)} </div>;
        },
      },
      {
        dataField: "harga_nota",
        text: "Harga Nota",
        headerClasses: "text-right",
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },

      {
        dataField: "harga",
        text: "Harga",
        headerClasses: "text-right",
        style: (cell, row, rowIndex, colIndex) => {
          if (row.status_valid === "CANC") {
            return {
              backgroundColor: "#F6A6AD",
            };
          }
          return {
            backgroundColor: "#c8e6c9",
          };
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "status_valid",
        text: "Status",
        headerClasses: "text-left",
        style: (cell, row, rowIndex, colIndex) => {
          if (row.status_valid === "CANC") {
            return {
              backgroundColor: "#F6A6AD",
            };
          }
          return {
            backgroundColor: "#c8e6c9",
          };
        },
        formatter: (cell) => {
          return cell === "CANC" ? "BATAL PEMBELIAN" : "PEMBELIAN";
        },
      },
    ];
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="kode_group"
              component={ReanderSelect}
              options={datagroup}
              onChange={(e) => this.setState({ kode_group: e })}
              label="Kode Group"
              placeholder="Pilih Kode Group"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="jenis_group"
              component={ReanderSelect}
              options={[
                {
                  value: "ALL",
                  name: "SEMUA",
                },
                {
                  value: "MUDA",
                  name: "MUDA",
                },
                {
                  value: "TUA",
                  name: "TUA",
                },
              ]}
              label="Jenis Group"
              placeholder="Pilih Jenis Group"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="kondisi"
              component={ReanderSelect}
              options={kondisi}
              label="Kondisi"
              placeholder="Pilih Kondisi"
            />
          </div>
          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>

          <div className="col-lg-12">
            <Tabel
              keyField="kode_barcode"
              data={this.props.export ? databarang : []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Transaksi Pembelian Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <ExportTransaksiPembelianPdfHistory
                  data={this.props.LaporanTransaksiPembelian}
                />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadFormLaporanPembelianBarangHistory = reduxForm({
  form: "HeadFormLaporanPembelianBarangHistory",
  enableReinitialize: true,
  validate: ValidasiLaporanKeuangan,
})(HeadFormLaporanPembelianBarangHistory);
// export default connect(null)(HeadFormLaporanPembelianBarangHistory);
export default connect((state) => {
  return {
    GetDataGroup: state.datamaster.GetDataGroup,
    datakondisi: state.datamaster.getDataKondisiBarang,
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFormLaporanPembelianBarangHistory);
